  // Funcie pentru a obine data i ora actual (Romnia)
  const getCurrentDateTime = () => {
    const now = new Date();
    // Crem o dat n fusul orar al Romniei (Europe/Bucharest)
    const romaniaTime = new Date(now.toLocaleString("en-US", { timeZone: "Europe/Bucharest" }));
    
    // Formatm data ca DD.MM.YYYY pentru afiare
    const day = String(romaniaTime.getDate()).padStart(2, '0');
    const month = String(romaniaTime.getMonth() + 1).padStart(2, '0');
    const year = romaniaTime.getFullYear();
    const dateDisplay = `${day}.${month}.${year}`; // DD.MM.YYYY
    
    // Pstrm formatul YYYY-MM-DD pentru input-ul de tip date
    const dateInput = romaniaTime.toISOString().split('T')[0]; // YYYY-MM-DD
    const time = romaniaTime.toTimeString().slice(0, 5); // HH:MM format 24h
    
    return { date: dateInput, time, dateDisplay };
  };

  // Funcie pentru a extrage sanciunile aplicate unei infraciuni
  const getSanciuniForInfraciune = (infraciuneText) => {
    if (!infraciuneText || infraciuneText.trim() === '') {
      return [];
    }

    const sanciuni = [];
    
    // Verificm dac baza de date este disponibil
    if (!databaseData || !databaseData.codPenal || !Array.isArray(databaseData.codPenal)) {
      return sanciuni;
    }
    
    // Extragem titlul din textul infraciunii
    const titleMatch = infraciuneText.match(/Art\.?\s*\d+\.?\d*\s*-\s*(.+)$/);
    const title = titleMatch ? titleMatch[1].trim() : infraciuneText.trim();
    
    // Cutm infraciunea n baza de date dup titlu
    const infraciune = databaseData.codPenal.find(item => 
      item && item.titlu && 
      item.titlu.trim() === title
    );
    
    if (!infraciune) {
      return sanciuni;
    }
    
    // Adugm arest
    if (infraciune.arestPeViata) {
      sanciuni.push('Arest pe via');
    } else if (infraciune.arestMin !== undefined && infraciune.arestMin !== null && infraciune.arestMin > 0 && 
        infraciune.arestMax !== undefined && infraciune.arestMax !== null && infraciune.arestMax > 0) {
      sanciuni.push(`${infraciune.arestMin} - ${infraciune.arestMax} luni`);
    } else if (infraciune.arestMin !== undefined && infraciune.arestMin !== null && infraciune.arestMin > 0) {
      sanciuni.push(`${infraciune.arestMin} luni`);
    } else if (infraciune.arestMax !== undefined && infraciune.arestMax !== null && infraciune.arestMax > 0) {
      sanciuni.push(`${infraciune.arestMax} luni`);
    }
    
    // Adugm amend
    if (infraciune.amendaMin && infraciune.amendaMax && 
        (infraciune.amendaMin > 0 || infraciune.amendaMax > 0)) {
      if (infraciune.amendaMin === infraciune.amendaMax) {
        sanciuni.push(`${infraciune.amendaMin} $`);
      } else {
        sanciuni.push(`${infraciune.amendaMin} - ${infraciune.amendaMax} $`);
      }
    }
    
    // Adugm puncte permis
    if (infraciune.punctePermis && infraciune.punctePermis > 0) {
      sanciuni.push(`${infraciune.punctePermis} puncte`);
    }
    
    // Adugm sanciuni booleene (doar dac sunt true)
    if (infraciune.anularePermis) {
      sanciuni.push('Anulare Permis');
    }
    
    if (infraciune.perchezitie) {
      sanciuni.push('Percheziie');
    }
    
    if (infraciune.ridicareVehicul) {
      sanciuni.push('Ridicare Vehicul');
    }
    
    if (infraciune.dosarPenal) {
      sanciuni.push('Dosar Penal');
    }
    
    return sanciuni;
  };

  // Funcie pentru verificarea incompatibilitilor ntre infraciuni
  const checkIncompatibilitati = useCallback(() => {
    const infractiuniSelectate = [
      cardData.incadrare.infractiune1,
      cardData.incadrare.infractiune2,
      cardData.incadrare.infractiune3
    ].filter(infractiune => infractiune && infractiune.trim() !== '');
    
    // Verificm doar dac avem cel puin 2 infraciuni
    if (infractiuniSelectate.length < 2) {
      return { areIncompatibilitati: false, mesaje: [] };
    }
    
    const incompatibilitatiGasite = [];
    
    // Verificm fiecare pereche de infraciuni folosind ID-urile
    for (let i = 0; i < infractiuniSelectate.length; i++) {
      for (let j = 0; j < infractiuniSelectate.length; j++) {
        const infractiune1Text = infractiuniSelectate[i];
        const infractiune2Text = infractiuniSelectate[j];
        
        // Srim aceeai poziie (i == j) pentru a nu verifica o infraciune cu ea nsi
        if (i === j) continue;
        
        // Gsim ID-ul pentru prima infraciune
        const infractiune1 = databaseData.codPenal.find(item => 
          `${item.cap} - Art. ${item.numarArticol} - ${item.titlu}` === infractiune1Text
        );
        
        // Gsim ID-ul pentru a doua infraciune
        const infractiune2 = databaseData.codPenal.find(item => 
          `${item.cap} - Art. ${item.numarArticol} - ${item.titlu}` === infractiune2Text
        );
        
        if (infractiune1 && infractiune2) {
          const id1 = infractiune1.id;
          const id2 = infractiune2.id;
          
          // Verificm dac este aceeai infraciune (duplicate)
          const esteAceeasiInfraciune = infractiune1Text === infractiune2Text;
          
          // Verificm dac exist incompatibilitate n ambele sensuri
          const incompatibilitate1to2 = infractiune1.incompatibilitati && 
            (infractiune1.incompatibilitati.includes(id2) || 
             infractiune1.incompatibilitati.includes(id2.replace('_vitezei_legale', '_legal')));
          
          const incompatibilitate2to1 = infractiune2.incompatibilitati && 
            (infractiune2.incompatibilitati.includes(id1) || 
             infractiune2.incompatibilitati.includes(id1.replace('_vitezei_legale', '_legal')));
          
          // Dac exist incompatibilitate n orice sens SAU este aceeai infraciune (dar nu aceeai poziie)
          if ((incompatibilitate1to2 || incompatibilitate2to1) || esteAceeasiInfraciune) {
            // Verificm dac nu am adugat deja aceast combinaie
            const existingCombo = incompatibilitatiGasite.find(item => 
              (item.infractiune1 === infractiune1Text && item.infractiune2 === infractiune2Text) ||
              (item.infractiune1 === infractiune2Text && item.infractiune2 === infractiune1Text)
            );
            
            if (!existingCombo) {
              incompatibilitatiGasite.push({
                infractiune1: infractiune1Text,
                infractiune2: infractiune2Text,
                motiv: esteAceeasiInfraciune ? 'Duplicate - aceeai infraciune selectat de mai multe ori' : 'Incompatibilitate nepermis'
              });
            }
          }
        }
      }
    }
    
    return {
      areIncompatibilitati: incompatibilitatiGasite.length > 0,
      mesaje: incompatibilitatiGasite
    };
  }, [cardData.incadrare.infractiune1, cardData.incadrare.infractiune2, cardData.incadrare.infractiune3, databaseData]);

