  // State pentru mesajele de incompatibilitate
  const [incompatibilitati, setIncompatibilitati] = useState([]);
  const calculateTotalAmenda = () => {
    let total = 0;
    const infractiuni = [
      cardData.incadrare.infractiune1,
      cardData.incadrare.infractiune2,
      cardData.incadrare.infractiune3
    ];
    
    infractiuni.forEach(infractiuneText => {
      if (infractiuneText && infractiuneText.trim() !== '') {
        const infraciune = databaseData.codPenal.find(item => 
          `${item.cap} - Art. ${item.numarArticol} - ${item.titlu}` === infractiuneText
        );
        
        if (infraciune && infraciune.amendaMin && infraciune.amendaMax) {
          // Adugm media amenzii (sau valoarea exact dac e aceeai)
          const amenda = infraciune.amendaMin === infraciune.amendaMax 
            ? infraciune.amendaMin 
            : Math.floor((infraciune.amendaMin + infraciune.amendaMax) / 2);
          total += amenda;
        }
      }
    });
    
    return total > 0 ? `${total} $` : '';
  };

  // Funcie pentru calcularea totalului arestului
  const calculateTotalArest = () => {
    let minTotal = 0;
    let maxTotal = 0;
    const infractiuni = [
      cardData.incadrare.infractiune1,
      cardData.incadrare.infractiune2,
      cardData.incadrare.infractiune3
    ];
    
    infractiuni.forEach(infractiuneText => {
      if (infractiuneText && infractiuneText.trim() !== '') {
        const infraciune = databaseData.codPenal.find(item => 
          `${item.cap} - Art. ${item.numarArticol} - ${item.titlu}` === infractiuneText
        );
        
        if (infraciune) {
          if (infraciune.arestPeViata) {
            // Arest pe via nlocuiete orice alt sentin
            return 'Arest pe via';
          }
          
          if (infraciune.arestMin && infraciune.arestMin > 0) {
            minTotal += infraciune.arestMin;
          }
          if (infraciune.arestMax && infraciune.arestMax > 0) {
            maxTotal += infraciune.arestMax;
          }
        }
      }
    });
    
    // Limitm maximul la 360 luni
    minTotal = Math.min(minTotal, 360);
    maxTotal = Math.min(maxTotal, 360);
    
    if (minTotal > 0 || maxTotal > 0) {
      if (minTotal === maxTotal) {
        return `${minTotal} luni`;
      } else {
        return `${minTotal} - ${maxTotal} luni`;
      }
    }
    
    return '';
  };

  // Funcie pentru calcularea totalului punctelor de permis
  const calculateTotalPuncte = () => {
    let total = 0;
    const infractiuni = [
      cardData.incadrare.infractiune1,
      cardData.incadrare.infractiune2,
      cardData.incadrare.infractiune3
    ];
    
    infractiuni.forEach(infractiuneText => {
      if (infractiuneText && infractiuneText.trim() !== '') {
        const infraciune = databaseData.codPenal.find(item => 
          `${item.cap} - Art. ${item.numarArticol} - ${item.titlu}` === infractiuneText
        );
        
        if (infraciune && infraciune.punctePermis && infraciune.punctePermis > 0) {
          total += infraciune.punctePermis;
        }
      }
    });
    
    return total > 0 ? `${total} puncte` : '';
  };

